
/* Los primeros cuatro hacen contraste */
/* error y background hacen contraste */
/* Blanco con negro */
:root {
    --primary: #6200ee;
    --primary-variant: #3700b3;
    --secondary: #03dac6;
    --secondary-variant: #018786;
    --background: #f9f9f9;
    --error: #b00020;
    --on-background: #000000;
    --background-logo: #1e252b;
    --height-header: calc( 120px/ 2 );
    --width-header: 120px;
    --blue-facebook: rgb(45, 136, 255);
    --oscuro: #18191a;
    --block-oscuro: #242526;
    --color-text: #e4e6eb;
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    background-color: var(--oscuro);
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Oxygen', sans-serif;
}

@media (min-width: 1600px) {
    .header-site {
        background-color: var(--background-logo);
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-template-rows: 1fr;
        height: var(--height-header);
        overflow: hidden;
    }
    
    .logo-header img {
        width: calc(var(--width-header) + 12%);
        height: calc(var(--height-header) + 12px);
    }

    .list-nav {
        height: var(--height-header);
        display: flex;
        align-items: center;
        justify-content: flex-end;
    }

    .list-nav li {
        list-style: none;
        margin: 0 8px;
    }

    .list-nav li a {
        font-family: 'Oxygen';
        text-decoration: none;
        color: var(--color-text);
    }

    .main-index {
        display: grid;
        grid-template-rows: repeat(1, 1fr);
        grid-template-columns: 25% 75%;
    }

    .menu-fixed {
        position: fixed;
        z-index: 1;
    }

    .menu-fixed ul li {
        font-family: 'Roboto', sans-serif;
        /* letter-spacing: 2px; */
        list-style: none;
        color: var(--color-text);
        padding: 12px;
        border-bottom: 1px solid #1e252b;
    }

    .section-article {
        position: relative;
        z-index: 1;
        margin: 8px;
        padding: 12px;
        -webkit-box-shadow:0 1px 3px rgba(0,0,0,.2);
        -moz-box-shadow:0 1px 3px rgba(0,0,0,.2);
        box-shadow:0 1px 3px rgba(0,0,0,.2);
        border-left: 1px solid rgba(0,0,0,.7);
    }

}